import gql from "graphql-tag";
import { withApollo } from 'react-apollo';
import {
  List,
  ListItem,
  ListItemContainer,
  Num,
  A
} from '../ApolloPostList/styles';

const GET_POSTS = gql`
  query allPosts($first: Int!, $skip: Int!) {
    allPosts(orderBy: createdAt_DESC, first: $first, skip: $skip) {
      id
      title
      votes
      url
      createdAt
    }
    _allPostsMeta {
      count
    }
  }
`
const skip = 0;
const variables = {
    first: 10,
    skip: 0
}

async function clientQuery(client) {
  var {data} = await client.query({ query: GET_POSTS, skip, variables});
  debugger;
  console.log(data)
  return data;
}

const App = ({client}) => {
  var {allPosts} = clientQuery(client);
  return (
    <List>
      {allPosts && allPosts.map((post, index) => (
        <ListItem key={post.id}>
          <ListItemContainer>
            <Num>{index + 1}. </Num>
            <A href={post.url}>{post.title}</A>
            <PostUpvoter id={post.id} votes={post.votes} />
          </ListItemContainer>
        </ListItem>
      ))}
    </List>
  )
}

export default withApollo(App);